import React, { useState } from "react";
import { players } from "./helpers/constants";

const PlayersTest = () => {
  const [tablePlayers, setPlayers] = useState(players);
  return (
    <div>
      <button onClick={() => setPlayers(players)}>Reset</button>
      <button onClick={() => setPlayers([{ name: "shit" }])}>
        Shit happened
      </button>
      {tablePlayers.map(player => player.name)}
    </div>
  );
};

export default PlayersTest;



